body > .wp-site-blocks > header nav ul.wp-block-navigation__container > li {
  display: inline-block;
  text-wrap: nowrap;
  padding: 5px 10px;
  position: relative;
}
body > .wp-site-blocks > header nav ul.wp-block-navigation__container > li .wp-block-navigation__submenu-icon {
  position: absolute;
  right: 10px;
  top: calc(50% - 5.3px);
}
